import React, { Component, useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import axios from 'axios';
import "bootstrap/dist/css/bootstrap.min.css";
import parametersURL from 'query-string';
import validator from 'validator';

class ResetPassWord extends Component {

    constructor(props) {
        super(props)
        

        this.state = {
            emailToken: '',
            loginPassword: '',
            confirmPassword: '',
            message:''
        }
        this.doResetPassword = this.doResetPassword.bind(this);
        this.getEmailToken = this.getEmailToken.bind(this);
        this.onChangeLoginPassword = this.onChangeLoginPassword.bind(this);
        this.onChangeConfirmPassword = this.onChangeConfirmPassword.bind(this);
        this.validatePassword = this.validatePassword.bind(this);
    }

    validatePassword(pass) {
        if (validator.isStrongPassword(pass, {
        minLength: 8,
        minLowercase: 1,
        minUppercase: 1,
        minNumbers: 1,
        minSymbols: 1
        })) {
            this.setState({
                message: ''
            })
            return(true);
        } else {
            this.setState({
                message: 'The password in not strong enough'
            })
            return(false);
        }
    }
  
    getEmailToken() {
        const value = parametersURL.parse(this.props.location.search);
        return value.emailToken;
    }

    onChangeLoginPassword(e) {
        this.setState({
            loginPassword: e.target.value
        });
    }

    onChangeConfirmPassword(e) {
        this.setState({
            confirmPassword: e.target.value
        });
    }

    componentDidMount() {

    }

    doResetPassword() {

        if (this.state.loginPassword === this.state.confirmPassword) {

            if (this.validatePassword(this.state.loginPassword ) && this.validatePassword(this.state.confirmPassword)) {

                this.obj = { 
                    emailToken: this.getEmailToken(),
                    password: this.state.loginPassword,
                    confirmpassword: this.state.confirmPassword
                };

                this.js = JSON.stringify(this.obj);

                this.config = {
                    method: 'post',
                    url: 'https://rate-my-city.herokuapp.com/ResetPwd',
                    headers: { 'Content-Type': 'application/json' },
                    data: this.js
                };

                axios(this.config)
                    .then(response => {
                        var res = response.data;
                        if (res.message) {
                            this.setState({
                                message: res.message
                            });
                        } else {
                            this.setState({
                                message: res.message,
                            });
                            this.returnFromReset();
                        }
                })
            }
        } else {
            this.setState({
                message: 'Passwords Not Matching!'
            })
        }
    }

    returnFromReset() {
        localStorage.clear();
        this.props.history.push("/");
    }

    handleClickCancel() {
        localStorage.clear();
        this.props.history.push("/");
    }

    render () {
        return (
            <div className="container-fluid h-custom">
              <div className="row d-flex justify-content-center align-items-center h-100">
                <div className="mt-5 col-xl-4">
                  <form >
        
                    <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '8vh'}}>
                        <h1>Reset Password</h1>
                    </div>
        
                    <div className="divider d-flex align-items-center my-4 mt-0">
                      <p className="text-center fw-bold mx-3 mb-4"></p>
                    </div>
        
        
                    <div className="form-outline mb-2">
                        <input type="password" autoFocus
                            className="form-control form-control-lg" 
                            value={this.state.loginPassword}
                            onChange={this.onChangeLoginPassword}
                            placeholder="Enter New Password" 
                        />
                        <label className="form-label">New Password</label>
                    </div>
                    <div className="form-outline mb-3">
                        <input type="password" 
                            className="form-control form-control-lg" 
                            value={this.state.confirmPassword}
                            onChange={this.onChangeConfirmPassword}
                            placeholder="Confirm password"
                        />
                        <label className="form-label">Confirm Password</label>
                    </div>
        
                    <div className="form-group row d-flex justify-content-center ">
                        <div className="col-md-4">
                            <button type="button" className="btn btn-secondary btn-lg form-control" onClick={this.doResetPassword}>Update</button>
                        </div>
        
                        <div className="col-md-4">
                            <button type="button" className="btn btn-dark btn-lg form-control" onClick={this.handleClickCancel}>Cancel</button>
                        </div>
        
                        <div className="divider d-flex align-items-center my-4">
                            <p className="text-center fw-bold mx-3 mb-1">{this.state.message}</p>
                        </div>
                    </div>
        
                  </form>
                </div>
              </div>
            </div>
        
        );
    }
}

export default withRouter(ResetPassWord);