function TopBorder()
{

    // const [modalShow, setModalShow] = React.useState(false);
    const history = useHistory();

    var userFullName = '';
    var buttonLabel = 'Login / Register';
    var validUsr = false;
    if ( localStorage.accessToken && localStorage.accessToken !== '' ) {
        userFullName = "Welcome: " + localStorage.userFirstName + ' ' + localStorage.userLastName;
        validUsr = true;
        buttonLabel = 'Logout'
    }

    const [show, toggle] = React.useState(validUsr);
    const ToggleShow = () => toggle(validUsr);


    function handleClick() {
        if ( localStorage.accessToken && localStorage.accessToken !== '' ) {
            userFullName = '';
            localStorage.clear();
            window.location.reload(false);
        } else {
            history.push("/login");
        }
    }

    function handleClickCity () {
        history.push("AddCity")
    }

    function handleClickSettings() {
        history.push("/Settings")
    }


    return(
      <div className="container-fluid h-custom mt-5">
        <p>&nbsp;</p>
        <div className="input-group mt-1 mb-2">

          {/* {show &&
            <div>
              <DropdownButton className="input-group-append icon-play" id="dropdown-basic-button" size="sm" variant="Secondary" menuVariant="secondary">
                <Dropdown.Item className="btn-group-sm" href="#/action-1" onClick={handleClickCity}>Add City</Dropdown.Item>
              </DropdownButton>
            </div>
          } */}
          

          <input className="form-control text-left fw-bold" disabled="disabled" placeholder={userFullName} />

          <div className="input-group-append" >
              <button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={handleClick}>{buttonLabel}</button>
          </div>


          {show &&
            <div className="input-group-append">
              <button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={handleClickSettings}>Settings</button>
            </div>
          }


          {/* <div className="input-group-append" >
              <button className="btn text-white fw-bold btn-outline-dark btn-secondary" onClick={() => setModalShow(true)}>Add City</button>
          </div>
          <MyVerticallyCenteredModal
            show={modalShow}
            onHide={() => setModalShow(false)}
          /> */}

        </div>
      </div>

    );
};

export default TopBorder; 
